import { Box } from '@material-ui/core';
import { ChangeEvent, useCallback, useState } from 'react';
import { Link, useHistory } from 'react-router-dom';
import { EmailOrPhone } from '../ui/EmailOrPhone';
import { IconFacebook, IconGoogle } from '../ui/Icons';
import { RoleInputField } from '../ui/RoleInputField';
import { SingleLineInput } from '../ui/SingleLineInput';
import {ConfirmOTP} from './ConfirmOTP';
import { Validators } from '../../core/validators';
import { __tr } from '../../i18n';
import { Routes } from '../../routes';
import styles from '../../styles/Signup.module.scss';
import { toast } from 'react-toastify';
import { Loader } from '../ui/Loader';
import { IAppState } from '../../state';
import { connect } from 'react-redux';
import { initializeState } from '../../state/middlewares';
import { AnyAction } from 'redux';
import { ThunkDispatch } from 'redux-thunk';
import { persistUname } from '../../core/utils';
import axios from 'axios';
import PasswordStrengthBar from 'react-password-strength-bar';

const mapState = (state: IAppState) => {
  return {
    user: state.userState.user,
  };
};


const mapDispatch = (dispatch: ThunkDispatch<IAppState, {}, AnyAction>) => {
  return {
    initialize: () => dispatch(initializeState()),
  };
};

type Props = ReturnType<typeof mapState> & ReturnType<typeof mapDispatch>;
function Signup(props: Props) {
  const [socialModeEnabled, setSocialModeEnabled] = useState(false);
  const [emailOrPhone, setEmailOrPhone] = useState('');
  //const [selectedRole, setSelectedRole] = useState(Roles[0].value);
  const [selectedRole, setSelectedRole] = useState('INFLUENCER'); 
  const [password, setPassword] = useState('');
  const [errors, setErrors] = useState<any>({});
  const [loading, setLoading] = useState(false);
  const [verifyOtp, setVerifyOtp] = useState(false);
  const[userId, setUserId] = useState();

  const history = useHistory();

  const onFormChange = useCallback((ev: ChangeEvent<HTMLFormElement>) => {
    setErrors({});
    setLoading(false);
  }, []);

  const googleLogin = () => {
    window.location.href= 'http://aa62100d41cee4c06a48c5dca26c6679-1265921153.ap-south-1.elb.amazonaws.com/api/auth/google';

  };

  const facebookLogin = () => {

  };

  const onSubmit = useCallback(
    (ev: ChangeEvent<HTMLFormElement>) => {
      ev.preventDefault();

      if (loading) {
        /// does nothing if there is an ongoing action
        return;
      }

      let form = ev.currentTarget;
      let newErrors: any = {};

      if (!socialModeEnabled) {
        let data = {
          email: Validators.isEmail(emailOrPhone) ? emailOrPhone : undefined,
          phone: Validators.isPhone(emailOrPhone) ? emailOrPhone : undefined,
          password: form.password.value,
          name: form.fullName?.value?.trim(),
          role: selectedRole,
        };

        if (!data.email && !data.phone) {
          newErrors.emailOrPhone = __tr('errorInvalidEmailOrPhone');
        }
        let passwordStatus=Validators.isValidPasswordNew(data.password)
        if (!passwordStatus.valid) {
          newErrors.password = __tr(passwordStatus.message);
        }

        if (!Validators.isValidName(data.name)) {
          newErrors.name = __tr('errorInvalidName');
        }

        console.log(data, newErrors.name);

        if (Object.keys(newErrors).length === 0) {
          setLoading(true);
          
          axios.post('/api/auth/register',data)
          .then((result) => {
            console.log("register result",result)
            //persistUname(result?.user.getUsername());
          if(result.data=="User Already Exists"){
         
            console.log(result)
            toast.error("User Already Exists");
          }else {
            setUserId(result.data);
             setVerifyOtp(true);
            // alert(result.data)
          }
    
            //history.push(Routes.VerifyOTP);
          })
          .catch((err) => {
             alert("Under ca")
           
            console.log(err);
            toast.error("User Registeration Failed");
          })
          
            .finally(() => {
              setLoading(false);
            });
        }
      }
      setErrors(newErrors);
    },
    [socialModeEnabled, emailOrPhone, selectedRole, loading, history]
  );

  const signin = () => {
	setLoading(true);
  let data = {
    email: Validators.isEmail(emailOrPhone) ? emailOrPhone : undefined,
    phone: Validators.isPhone(emailOrPhone) ? emailOrPhone : undefined,
    password: password,
  };
  console.log("goinf login data",data)
  axios.post('/api/auth/login',data).then(res=>{
    setLoading(false);
    localStorage.setItem('token',res.data.token);
    return history.replace(Routes.Home);
  }).catch((err) => {
        setLoading(false);
        toast.error(err.message);
      });
  }

  if(verifyOtp) {
	  return (<ConfirmOTP signIn={signin} user={userId}/>);
  } else {
  return (
    <div className={styles.loginWrapper}>
      <h3>{__tr('welcomeToWobb')}</h3>
      <div className={styles.formWrapper}>
        <h3>{__tr('signup')}</h3>
        {loading && <Loader />}
        <form onChange={onFormChange} action='' className={styles.form} onSubmit={onSubmit}>
          {socialModeEnabled && (
            <div className={styles.socialAccount}>
              <div className={styles.header}>
                <span className={styles.name}>John Deco</span>
                <button className={styles.btnNotYou} onClick={() => setSocialModeEnabled(false)}>
                  {__tr('btnNotYou')}
                </button>
              </div>
              <div className={styles.content}>
                <span className={styles.email}>john@sample.com</span>
              </div>
            </div>
          )}
          {!socialModeEnabled && (
            <Box marginY={2}>
              <EmailOrPhone
                label={__tr('labelEmailOrPassword')}
                placeholder='johndoe@example.com'
                onChange={(val) => setEmailOrPhone((val.value).toLowerCase())}
                error={errors.emailOrPhone}
              />
            </Box>
          )}
          <Box marginY={2}>
            <SingleLineInput
              label={__tr('labelCreatePassword')}
              type='password'
              name='password'
              placeholder={__tr('placeholderSignupPassword')}
              inputProps={{
                onChange: (ev) => setPassword(ev.currentTarget.value),
                value: password,
              }}
              error={errors.password}
            />
            {password && <PasswordStrengthBar password={password} />}
          </Box>
          {!socialModeEnabled && (
            <Box marginY={2}>
              <SingleLineInput
                label={__tr('labelName')}
                type='text'
                name='fullName'
                placeholder='John'
                error={errors.name}
              />
            </Box>
          )}
          <Box marginY={2}>
            <RoleInputField label={__tr('labelSelectRole')} value={selectedRole} onChange={setSelectedRole} />
			{/* <RoleInputField label={__tr('labelSelectRole')} value={selectedRole}  /> */}
          </Box>
          <Box marginY={2}>
            <p className={styles.textAcceptTerms}>
              {__tr('byRegistering')}, {__tr('IAgreeTo')}
              <Link to={Routes.TermsAndCondition} className={styles.link}>
                {__tr('termsAndConditions')}
              </Link>
              {__tr('and')}
              <Link to={Routes.PrivacyPolicies} className={styles.link}>
                {__tr('privacyPolicies')}
              </Link>
            </p>
          </Box>
          {!loading && (
            <Box marginY={2}>
              <button type='submit' className={styles.btnSubmit}>
                <span className={styles.text}>{__tr('btnAgreeAndJoin')}</span>
              </button>
            </Box>
          )}
          <Box marginY={4} display='flex' flexDirection='row' alignItems='center' justifyContent='center'>
            <div className={styles.lined}>
              <div className={styles.text}>{__tr('or')}</div>
            </div>
          </Box>
          {!socialModeEnabled && (
            <>
            <Box marginY={2}>
              <button
                type='button'
                onClick={() => {googleLogin() ;setSocialModeEnabled(false)}}
                className={styles.googleSignup}
              >
                <span className={styles.icon}>
                  <IconGoogle size={24} />
                </span>
                <span className={styles.text}>{__tr('joinWithGoogle')}</span>
              </button>
            </Box>
            {/* <Box marginY={1}>
                <button
                  type='button'
                  onClick={() => {facebookLogin() ;setSocialModeEnabled(false)}}
                  className={styles.googleSignup}
                >
                  <span className={styles.icon}>
                    <IconFacebook size={24} />
                  </span>
                  <span className={styles.text}>{__tr('joinWithFacebook')}</span>
                </button>
              </Box> */}
            </>
          )}
          <Box marginY={2} display='flex' flexDirection='row' alignItems='center' justifyContent='center'>
            <span className={styles.callToSignup}>{__tr('alreadyMember')}</span>
            <Link to={Routes.Login} className={styles.signinLink}>
              {__tr('btnSignin')}
            </Link>
          </Box>
        </form>
      </div>
    </div>
  );
		  }
}
export default connect(mapState, mapDispatch)(Signup);
