function ApplicantRow(props: { checkchange(check:boolean,id:string):any,filter: string, applicant: Application, 
  bidPriceFlag: boolean, selected:boolean, setForApproval(check:boolean): void,approveList: Application[] }) {


    const [appList, setAppList] = useState<Application[]>([]);

    useEffect(()=>{

      setAppList(props.approveList);

    },[])
  
    const dispatch = useDispatch();
  let payout = 0;
  let bidprice =0;
  if(props.filter === 'shortlisted' || props.filter === 'hired') {
    const pays = props?.applicant?.payouts?.filter(a=>a.type==='VARIABLE' || a.type==='FIXED');
    pays?.forEach(p=>{
      if(p.type==='VARIABLE')
      payout+=Number(p.maxCash?.amount);
      else
      payout+=Number(p.cash?.amount);
    })
  }

  if(props?.bidPriceFlag) {
    const pays = props?.applicant?.payouts?.filter(a=>a.type==='VARIABLE') ?? [];
    if(pays?.length > 0) {
      bidprice=Number(pays[0].cash?.amount)
    }
    
  }
   // @ts-ignore: Object is possibly 'null'.
  const escrowList = props?.applicant?.milestones.filter(a=>  a.status === "Activated" || a.status === "Submitted" || a.status === "Approved");
   // @ts-ignore: Object is possibly 'null'.
  const percentage = escrowList.reduce((a,b)=>{return (a+Number(b?.percentage))},0);
  //const approveList = [...props.approveList];
  const approveList = useApplicantsApprove();
  console.log('approveListapproveList',approveList);
  const approveMilestone = useCallback((checked:boolean,indx:number,status: string, applicantForAp:Application ) => {

    if(checked) {
      //approveList.push(props.applicant);
      const a = {...applicantForAp};
      a.selectedIdx = indx;
      a.actionOn = status;
      dispatch(setApplicantsApprove([a],"add"));
     // dispatch(setApplicantsApprove([...approveList,a ]));
      setAppList([...approveList,a ]);

    } else {
     
      const a = approveList.filter(a=> a._id ===applicantForAp._id  && a.selectedIdx === indx);
      dispatch(setApplicantsApprove(a,"remove"));
      //if(idx >= 0) {
      //   approveList.splice(idx,1);
      //   dispatch(setApplicantsApprove(approveList,"remove"));
      //   setAppList(approveList);
      //}
    }
    if(approveList.length > 0) {
      props.setForApproval(true);
    } else {
      props.setForApproval(false);
    }  
  }, [appList]);

  const isChecked = (i:number) => {
    const find = approveList.filter(a=>a._id === props?.applicant?._id);
    if(find.length > 0) {
      let ret = false;
      find.forEach(f=>{
        if(f.selectedIdx === i) ret=true;
      })
      return ret;
    } else {
      return false;
    }
  }


  const engagement=Math.round((Number(props?.applicant?.engagement) + Number.EPSILON) * 100) / 100;
  
 
  if (  props.filter === 'hired') {
    return (
      <TableRow>
        {/* <TableCell>
          <Checkbox checked={props.selected}/>
        </TableCell> */}
        <TableCell>
          <ApplicantItem
            name={props?.applicant?.influencerName || ''}
           // progress={props?.applicant?.progress}
            socialaccount={props?.applicant?.socialaccount || ''}
            image={props?.applicant?.imgUrl}
          />
        </TableCell>
        <TableCell>
        <div style={{display:'flex', flexDirection: 'row'}}>
          {props?.applicant?.milestones?.map((mil,i) =>
          {
            return (
              <div style={{display:'flex', flexDirection: 'column', marginLeft: '10px', alignItems: 'center',
              justifyContent: 'center'}}>
              <Checkbox style={{float:'left'}} 
                disabled={mil?.status === "Approved"}
              checked={isChecked(i)} onChange={(e)=>{approveMilestone(e.target.checked,i, mil?.status ?? 'Inactive', props?.applicant)}}/>
              <p style={{marginTop:'0px', whiteSpace: 'nowrap'}}>Milestone {i+1}</p>
              <p style={{marginTop:'-10px',color: mil?.status === 'Approved' ? 'green' : (mil?.status === 'Inactive' || !mil?.status ? '#848484' : 'blue') }}>{mil?.status ? mil.status : 'Inactive'}</p>
          </div>
            )
          })}
          
        </div>



        {/* <FormControl component="fieldset">
      <RadioGroup row>
          
              <FormControlLabel
                control={<Checkbox size='small' color='primary' />}
                //checked={!checked}
                //onClick={()=>{setchecked(false)}}
                //defaultChecked={true}
                label={'Milestone 1'}
              />
              <FormControlLabel
                control={<Checkbox size='small' color='primary' />}
                //checked={checked}
                //onClick={()=>{setchecked(true)}}
                //defaultChecked={false}
                label={'All Milestones'}
              />
        </RadioGroup>
        </FormControl> */}
          {/* <ApplicantDeliverables /> */}
        </TableCell>
        <TableCell>
          <Box display='flex' flexDirection='column' alignItems='center' justifyContent='center'>
            <Box
              height={30}
              display='flex'
              flexDirection='column'
              alignItems='center'
              justifyContent='center'
            >
              <span>Escrow : {(percentage/100)*Number(payout)}</span>
            </Box>
            <Box
              height={30}
              display='flex'
              flexDirection='column'
              alignItems='center'
              justifyContent='center'
            >
              <span>Paid : 0</span>
            </Box>
            <Box
              height={30}
              display='flex'
              flexDirection='column'
              alignItems='center'
              justifyContent='center'
            >
              <span>Total : {Number(payout)}</span>
            </Box>
          </Box>
        </TableCell>
      </TableRow>
    );
  }

  return (
    <TableRow>
      <TableCell>
        {console.log('props.selected ',props.selected)}
        <Checkbox checked={props?.selected} onChange={(e) => {
									  props.checkchange(e.target.checked,props?.applicant?._id);
									}}/>
      </TableCell>
      <TableCell>
      <ApplicantItem
            name={props?.applicant?.influencerName || ''}
           // progress={props?.applicant?.progress}
            socialaccount={props?.applicant?.socialaccount || ''}
            image={props?.applicant?.imgUrl}
          />
      </TableCell>
      <TableCell>{props?.applicant?.followers ? (props?.applicant?.followers) : ''}</TableCell>
      <TableCell>{engagement}{engagement && engagement > 0 ? '%' : '-'}</TableCell>
      {props?.bidPriceFlag && props.filter === 'received' && <TableCell>{bidprice}</TableCell>}
      {props.filter === 'shortlisted' && 
      <TableCell>{Number(payout) > 0 ? ''+Number(payout) : '-'}</TableCell>}
      <TableCell>
        <ApplicableActions filter={props.filter} applicant={props?.applicant} />
      </TableCell>
    </TableRow>
  );
}

